.Search_search__DeuRc {
  width: 100%;
}
.Search_search__DeuRc button {
  width: 30%;
  background: #32d600de;
  border: 1px solid #32d600de;
}
.Search_search__DeuRc button:focus {
  outline: none;
}
.Search_search__DeuRc button:hover,
.Search_search__DeuRc button:active {
  background-color: #3cff00;
  border-color: #3cff00;
}
.Search_search__DeuRc input {
  font-size: 100%;
  margin: 0 auto;
  padding: 0.5%;
  border-radius: 0.2rem;
  background-color: #9191917a;
  border: none;
  width: 50%;
  display: block;
  transition: all 0.3s;
}
.Search_search__DeuRc input:focus {
  outline: none;
  box-shadow: rgba(0, 0, 0, 0.45) 0px 25px 20px -20px;
}
.Search_search__DeuRc label {
  margin: 0.5%;
  font-weight: bold;
  font-size: 100%;
  display: block;
  transition: all 0.3s;
  -webkit-transform: translateY(0rem);
          transform: translateY(0rem);
}
.Search_search__DeuRc input:placeholder-shown + label {
  opacity: 0;
  visibility: hidden;
  -webkit-transform: translateY(-4rem);
  transform: translateY(-4rem);
}

@media only screen and (max-width: 600px) {
  .Search_search__DeuRc input {
    padding: 2.5%;
    width: 75%;
  }
  .Search_search__DeuRc label {
    font-size: 70%;
  }
  .Search_search__DeuRc button {
    width: 40%;
    margin: 2%;
  }
}

